import { Directive, ElementRef , Input   } from '@angular/core';
import { CdsTree } from '@cds/core/tree-view';

@Directive({ selector: 'cds-tree' })
export class CdsTreeDirective {
  protected element: CdsTree;

  get multiSelect() {
    return this.element.multiSelect;
  }
  @Input() set multiSelect(value: boolean | '') {
    this.element.multiSelect = value === '' ? true : value;
  };


  constructor(elementRef: ElementRef) {
    this.element = elementRef.nativeElement;
  }
}
